
import { defineComponent } from "@vue/runtime-core";
import { VIcon } from "vuetify/components/VIcon";


export default defineComponent({
  name: "GifPlayPause",

  components: {
    'v-icon': VIcon,
  },
  

  props: {
    stillOnly : {
      type: Boolean,
      default: false,
      required: false,
    },

    startPaused: {
      type: Boolean,
      default: false,
      required: false,
    },

    id: {
      type: String,
      default: "wcag-gif",
      required: false,
    },
    still: {
      type: String,
      required: true,
    },
    gif: {
      type: String,
      required: true,
    },
    alt: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      stayPaused: this.stillOnly,
      pause: this.stillOnly || this.startPaused,
    };
  },

  mounted() {
    const mediaQuery = window.matchMedia("(prefers-reduced-motion: reduce))");
    const details = document.querySelector(".wcag-gif-container > details") as HTMLDetailsElement;

    if (mediaQuery.matches && details) {
      this.stayPaused = true;
      details.removeAttribute("open");
      return;
    } else if (details) {
      details.open = ! (this.startPaused || this.stillOnly);
    }
  },

  methods: {

    onClick(): void {
      if (this.stayPaused) {
        return;
      } 

      this.pause = !this.pause;
      
    },
  }

});

