
import { defineComponent } from 'vue';

export default defineComponent({

  name: 'ImageLabel',

  props: {

    id: {
      type: String,
      default: ''
    },
    
    image: {
      type: String || null,
      default: null
    },

    altText: {
      type: String || null,
      default: null
    },
    backgroundColor: {
      type: String,
      default: 'transparent'
    },
    backgroundOpacity: {
      type: Number,
      default: 1
    },
    color: {
      type: String,
      default: 'white'
    },

    width: {
      type: String,
      required: true
    },

    height: {
      type: String,
      required: true
    },

    fontSize: {
      type: String,
      default: null
    },

    fontWeight: {
      type: String,
      default: null
    },

    lineHeight: {
      type: String,
      default: null
    },

    border: {
      type: String,
      default: null
    },

    borderRadius: {
      type: String,
      default: null
    },
    // hovered, active, and focused states
    
    hoveredColor: {
      type: String,
      default: null
    },
    activeColor: {
      type: String,
      default: null
    },
    focusedColor: {
      type: String,
      default: null
    },
    
    hoveredBorder: {
      type: String,
      default: '2px solid white'
    },
    activeBorder: {
      type: String,
      default: null
    },
    focusedBorder: {
      type: String,
      default: null
    },
  },

  data() {
    return {
      hovered: false,
      active: false,
      focused: false
    };
  },

  computed: {

    containerCSS() {
      const css = {
        color: this.color,
        width: `calc(${this.width}-2px)`,
        height: this.height,
        border: this.border,
        borderRadius: this.borderRadius
      };

      if (this.hovered && this.hoveredBorder) {
        return {...css, border: this.hoveredBorder };
      }

      if (this.active && this.activeBorder) {
        return {...css, border: this.activeBorder };
      }

      if (this.focused && this.focusedBorder) {
        return {...css, border: this.focusedBorder };
      }

      return css;

      
    },

    textCSS() {

      let css = {
        fontSize: this.fontSize,
        fontWeight: this.fontWeight,
        lineHeight: this.lineHeight
      } as object as Record<string, string>;

      if (this.hovered || this.focused) {
        css = {...css, fontWeight: 'bold' };
      }
      
      // if hovered
      if (this.hovered && this.hoveredColor) {
        return {...css, color: this.hoveredColor,  };
      }

      // if active
      if (this.active && this.activeColor) {
        return {...css, color: this.activeColor, fontWeight: 'bold' };
      }

      return {...css, color: this.color };
    },

    backgroundCSS() {
      const css = { backgroundColor: this.backgroundColor, opacity: this.backgroundOpacity};
      
      return css;
    }

  },



});

